<template>
  <div  class="wyu-video-upload">
    <el-upload
        class="avatar-uploader"
        :action="videoUpload"
        :show-file-list="false"
        :on-success="handleAvatarSuccess"
        :before-upload="beforeAvatarUpload">
        <i v-if="showVideo!=''" class="el-icon-refresh avatar-uploader-icon">重新上传</i>
        <i v-else class="el-icon-upload avatar-uploader-icon"></i>
    </el-upload>
  </div>
</template>

<script> 
import * as setting from '../../public/config.js';
export default {
  name: 'myuVideo',
  props: ['video'],
  data(){
      return {
          showVideo: '',
          videoUpload:'',
      };
  },
  watch:{
      video(val){
          this.showVideo = val;
      }
  },
  created(){
       this.videoUpload = setting.API_HOST +'api/upload/videoUpload?token='+encodeURI(this.$store.getters.adminToken);
       this.showVideo = this.video ? this.video : '';
  },
  methods:{
      handleAvatarSuccess(res, file) {
          this.showVideo = res.data.video_url;
          this.$emit('change',this.showVideo);
          this.$emit('input',this.showVideo);
      },
      beforeAvatarUpload(file) {
          console.log(file);
        const isJPG = file.type === 'video/mp4';
        const isLt5M = file.size / 1024 / 1024 < 5;

        if (!isJPG) {
          this.$message.error('视频只支持MP4格式');
        }
        if (!isLt5M) {
          this.$message.error('视频不能超过 5MB!');
        }
        return isJPG && isLt5M;
      }
  }
}
</script>

<style scoped>
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
