<template>
    <div>
        <TopNav title="编辑优惠券"></TopNav>
        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <div class="wyu-tips wyu-tips-info" style="margin-bottom:10px;">
                    <p class="tit">注意：只有优惠券领取类型为免费领取时才会在小程序弹出并可领取</p>
                </div>
                <el-form :model="goodsDetail.detail" ref="goodsInfo" size="mini" label-width="100px">
                    <div class="wyu-title">
                        <h2>这里编辑优惠券的信息</h2>
                    </div>
                    <el-form-item label="优惠券名称" >
                        <el-input v-model="goodsDetail.detail.name" style="width: 500px;"></el-input>
                    </el-form-item>

                    <el-form-item label="抵用金额" prop="money">
                        <el-input v-model="goodsDetail.detail.money" style="width: 500px"></el-input>
                    </el-form-item>

                    <el-form-item label="抵用条件金额" prop="condition">
                        <el-input v-model="goodsDetail.detail.condition" style="width: 500px"></el-input>
                    </el-form-item>

                    <el-form-item label="商品分类" prop="cat_id">
                        <product-category @selCat="getCatId" :cat-id="goodsDetail.detail.cat_id" :p-cat-id="goodsDetail.detail.p_cat_id"></product-category>
                    </el-form-item>

                    <el-form-item label="全部个数" prop="create_num">
                        <el-input v-model="goodsDetail.detail.create_num" style="width: 500px"></el-input>
                    </el-form-item>

                    <el-form-item label="开始领取时间" prop="send_start_time">
                        <el-date-picker v-model="goodsDetail.detail.send_start_time" type="datetime"
                                placeholder="选择日期时间" align="right" :picker-options="pickerOptions1">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="结束领取时间" prop="send_end_time">
                        <el-date-picker v-model="goodsDetail.detail.send_end_time" type="datetime"
                                        placeholder="选择日期时间" align="right" :picker-options="pickerOptions1">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="开始使用时间" prop="expire_start_time">
                        <el-date-picker v-model="goodsDetail.detail.expire_start_time" type="datetime"
                                        placeholder="选择日期时间" align="right" :picker-options="pickerOptions1">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="结束领取时间" prop="expire_end_time">
                        <el-date-picker v-model="goodsDetail.detail.expire_end_time" type="datetime"
                                        placeholder="选择日期时间" align="right" :picker-options="pickerOptions1">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="可领类型">
                        <el-select v-model="goodsDetail.detail.type"   placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in goodsDetail.couponUseType" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="使用范围">
                        <el-select v-model="goodsDetail.detail.userarea"   placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in goodsDetail.useArea" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="发放类型" >
                        <el-select v-model="goodsDetail.detail.type"   placeholder="请选择">
                            <el-option label="请选择" :value="-999"></el-option>
                            <el-option v-for="item in goodsDetail.couponType" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="排序">
                        <el-input v-model="goodsDetail.detail.orderby" style="width: 500px;"></el-input>
                        <p class="wyu-form-tips">数字越大越靠前</p>
                    </el-form-item>

                    <el-form-item label="上/下架">
                        <el-switch v-model="goodsDetail.detail.is_online" :active-value="1" :inactive-value="0"></el-switch>
                    </el-form-item>
                </el-form>
            </div>
        </div>
        <div class="wyu-btn-div">
            <el-button size="small" type="primary"  @click="editGoods()" >提交修改</el-button>
            <router-link to="/marketing/coupon/index"  style="margin-left: 10px"><el-button size="small">取消</el-button></router-link>
        </div>
    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import ImgUploads from "../../../components/Photos";
    import HtmlEditor from "../../../components/HtmlEditor";
    import ProductCategory from "@/pages/product/components/ProductCategory";

    export default {
        name: 'couponEdit',
        components: {
            ProductCategory,  HtmlEditor, ImgUploads, TopNav
        },
        data() {
            let checkCatSelect = (rule, value, callback) => {
                if (value <= 0) {
                    callback(new Error("请选择二级分类"));
                }
                callback();
            };

            return {
                loading:false,
                active: 1,
                typeId: 0,
                goodsDetail: {
                    detail: {
                        cat_id: 0,//分类ID
                        p_cat_id: 0,//为可视化部分冗余的 父级分类ID
                        name: "", //优惠券名称
                        money: 0,//品牌ID
                        condition: 0,//品牌ID
                        create_num: 0,//库存数量
                        send_num: 0,
                        use_num: 0,
                        send_start_time: '',//市场价
                        send_end_time: '',//市场价
                        expire_start_time: '',//市场价
                        expire_end_time: '',//市场价
                        type: -999,//本店价
                        userarea: -999,//本店价
                        receivetype: -999,
                        is_online: 0,
                        orderby: 0,
                    },

                },
                pickerOptions1: {
                    shortcuts: [{
                        text: '今天',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '昨天',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '一周前',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                },


            }
        },

        created() {
            this.getDetail();
        },

        methods: {
            getDetail(){
                this.loading = true;
                api.post("/shop/admin.marketing.coupon/detail", {
                    appToken: this.$store.getters.appToken,
                    coupon_id:this.$route.params.id,
                }).then(res => {
                    this.loading = false;
                    this.goodsDetail = res;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            getCatId(val) {
                this.goodsDetail.detail.p_cat_id = +val.p_cat_id;
                this.goodsDetail.detail.cat_id = +val.cat_id;
            },

            editGoods() {
                this.loading  = true;
                console.log(this.goodsDetail.detail);
                api.post("/shop/admin.marketing.coupon/edit", {
                    appToken: this.$store.getters.appToken,
                    coupon_id:this.$route.params.id,
                    cat_id: this.goodsDetail.detail.cat_id,  //分类ID
                    p_cat_id: this.goodsDetail.detail.p_cat_id,  //为可视化部分冗余的 父级分类ID
                    name: this.goodsDetail.detail.name,  //优惠券名称
                    money: this.goodsDetail.detail.money,  //优惠券名称
                    condition: this.goodsDetail.detail.condition,  //商品评论数量
                    create_num: this.goodsDetail.detail.create_num,  //商品评论数量
                    send_num: this.goodsDetail.detail.send_num,  //商品评论数量
                    use_num: this.goodsDetail.detail.use_num,  //商品评论数量
                    send_start_time: this.goodsDetail.detail.send_start_time,  //商品评论数量
                    send_end_time: this.goodsDetail.detail.send_end_time,  //商品评论数量
                    expire_start_time: this.goodsDetail.detail.expire_start_time,  //商品评论数量
                    expire_end_time: this.goodsDetail.detail.expire_end_time,  //商品评论数量
                    type: this.goodsDetail.detail.type,  //商品评论数量
                    userarea: this.goodsDetail.detail.userarea,  //商品评论数量
                    receivetype: this.goodsDetail.detail.receivetype,  //商品评论数量
                    is_online: this.goodsDetail.detail.is_online,  //商品评论数量
                    orderby: this.goodsDetail.detail.orderby,  //商品评论数量
                }).then(res => {
                    this.loading = false;
                    this.$message({
                        message: '编辑成功',
                        type: 'success'
                    });
                }).catch((error) => {
                    this.loading = false;
                });
            },
        }
    }
</script>
