                        </transition>
                    </div>
                </div>


                <div v-for="(item,index) in htmldata" :key="index" :style="(item.mgTop ? 'margin-top:'+item.mgTop+'px;' : '' )+(item.mgBt ? 'margin-bottom:'+item.mgBt+'px;' : '')" class="item">
                    <div  :class="editIndex==index ? 'show-item on' : 'show-item'" :style="styleByIndex(index)">
