                        <el-radio  style="margin-left:10px;" v-model="htmldata[index].type" :label="2">居右</el-radio>
                        <el-radio style="margin-left:10px;" v-model="htmldata[index].type" :label="3">首行缩进</el-radio>
                    </el-form-item>
                    <el-form-item v-if="item.mdl!='txt'" label="排列方式">
                        <el-radio v-model="htmldata[index].type" :label="0">居左</el-radio>
                        <el-radio  style="margin-left:10px;" v-model="htmldata[index].type" :label="1">居中</el-radio>
                        <el-radio  style="margin-left:10px;" v-model="htmldata[index].type" :label="2">居右</el-radio>
                    </el-form-item>
                    <el-form-item v-if="item.mdl=='txt'" label="字体大小">
