                    </el-form-item>
                    <el-form-item v-if="htmldata[index].isBgPic == 1" label="背景图片">
                        <wyu-upload  v-model="htmldata[index].bgPic" :img="htmldata[index].bgPic"></wyu-upload>
                    </el-form-item>
                    <el-form-item v-if="htmldata[index].isBgPic == 1" label="背景排列">
                        <el-radio v-model="htmldata[index].bgPicType" :label="0">顶部居中</el-radio>
                        <el-radio  style="margin-left:10px;" v-model="htmldata[index].bgPicType" :label="1">容器平铺</el-radio>
                    </el-form-item>

                    <el-form-item  label="背景颜色">
                        <el-color-picker  v-model="htmldata[index].bg"  size="mini"></el-color-picker>
                    </el-form-item>
                    <el-form-item  label="上内边距">
