<script>
    const  txtObj = { //文本的默认熟悉
        mdl:'txt',
        type:0,// 0代表居左
        txt:'请输入内容',//默认内容
        fontsize:14,
        isb:0,
        color:"#333333",
        lineH:20,  //行高
        isBgPic:0,//是否是背景图片
        bg:'#ffffff',//背景
        bgPic:'',
        bgPicType:0,//背景的类型
        pt:5,//头部
        pl:5,
        pr:5,
        pb:5,
        mgTop:0,//距离上面
        mgBt:0,//距离下面
    };
    const videoObj = {
        mdl:'video',
        type:0,//对齐方式
        video:'',//视频地址
        isBgPic:0,//是否是背景图片
        bg:'#ffffff',//背景
        bgPic:'',
        bgPicType:0,//背景的类型
        pt:5,//头部
        pl:5,
        pr:5,
        pb:5,
        mgTop:0,//距离上面
        mgBt:0,//距离下面
    };

    const  picObj={
        mdl:'pic',
        type:0,//对齐方式
        img:'',//图片地址
        isBgPic:0,//是否是背景图片
        bg:'#ffffff',//背景
        bgPic:'',
        bgPicType:0,//背景的类型
        pt:5,//头部
        pl:5,
        pr:5,
        pb:5,
        mgTop:0,//距离上面
        mgBt:0,//距离下面
    };
    import WyuUpload from "@/components/Photos";
    import WyuVideo from "@/components/Video";
    export default {
        name: 'htmlEditor',
        props:['html','title'],
        components: {WyuUpload,WyuVideo},
        data(){
            return {
                addBtnIndex: -2, // -1 是第一个默认的模块
                htmldata:[],
                editIndex:-1,//编辑的模块是
                showType:0,
            };
        },
        watch:{
            html(val){

                if(val != '' && val != this.htmldata){
                    this.htmldata = val;
                }
            },
            htmldata:{
                handler(newName, oldName) {
                    if(this.htmldata != this.html){
                        this.$emit('input',this.htmldata);
                    }
                },
                immediate: true,
                deep: true
            }

        },
        created() {
            if(this.html  !=this.htmldata){
                this.htmldata = this.html;
            }
        },
        methods:{
            styleByIndex(index){
                if(this.htmldata[index]){
                    let item = this.htmldata[index];
                    var style = '';
                    if(item.mdl == 'txt'){

                        style+="font-size:"+item.fontsize+'px;';
                        style+="color:"+item.color+';';
                        style+="line-height:"+item.lineH+'px;'
                        style+='word-wrap:break-word;';
                        if(item.isb == 1){
                            style+='font-weight:bold;';
                        }
                    }

                    switch(item.type){
                        case 1:
                            style+='text-align:center;';
                            break;
                        case 2:
                            style+='text-align:right;';
                            break;
                        case 3:
                            style+='text-indent:'+(item.fontsize*2)+'px;';
                            break;
                        default:
                            style+='text-align:left;';
                            break;
                    }

                    if(item.pt){
                        style+='padding-top:'+item.pt+'px;';
                    }
                    if(item.pl){
                        style+='padding-left:'+item.pl+'px;';
                    }
                    if(item.pr){
                        style+='padding-right:'+item.pr+'px;';
                    }
                    if(item.pb){
                        style+='padding-bottom:'+item.pb+'px;';
                    }

                    if(item.isBgPic && item.bgPic!=''){
                        if(item.bgPicType == 0){
                            style+='background: '+item.bg+' url('+item.bgPic+') no-repeat center 0';
                        }else{
                            style+='background: '+item.bg+' url('+item.bgPic+')';
                        }
                    }else{
                        style+='background:'+item.bg+';';
                    }




                    return style;
                }else{
                    return '';
                }
            },
            addTxt(index){
                var obj = JSON.parse(JSON.stringify(txtObj));
                this.addObj(obj,index);
            },
            addVideo(index){
                var obj = JSON.parse(JSON.stringify(videoObj));
                this.addObj(obj,index);
            },
            addPhoto(index){
                var obj = JSON.parse(JSON.stringify(picObj));
                this.addObj(obj,index);
            },
            addObj(obj,index){
                if(this.htmldata == ''){
                    index  = 0;
                    this.htmldata.push(obj);
                }else{
                    index = index + 1; //指定位置
                    this.htmldata.splice(index,0,obj);

                }
                this.editIndex = index;
                this.addBtnIndex = -2; //不显示任何添加的地方了
            },
            deleteObj(index){
                this.htmldata.splice(index,1);
                this.editIndex = -1;
                this.addBtnIndex = -2;
            },
        },
    }
</script>

